.navbar{display:flex;flex-direction:column;position:fixed;top:0;left:0;width:250px;height:100%;background-color:var(--background);box-shadow:2px 0px 5px rgba(0,0,0,.1);z-index:1000;padding-top:20px;padding-bottom:20px;gap:1rem}.left{display:flex;flex-direction:column;gap:1rem;align-items:flex-start;width:100%}.menuItem,.menuItemActive{display:flex;flex-direction:row;align-items:center;gap:10px;padding:10px;cursor:pointer;text-decoration:none;color:var(--on-surface);transition:background-color .3s;border-radius:5px;width:100%}.menuItemActive{background-color:var(--primary);color:#fff}.right{display:none}.dropdown{position:relative}.avatar{display:none}.walletBtn{display:none}